.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding-right: 10%;
  padding-left: 10%;
  background-color: #326EAA;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);

  &__logo-link {
    height: 38px;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    font-size: 14px;
    color: #fff;
    padding: 26px 30px;
    text-transform: uppercase;
    position: relative;
    transition: background-color 0.5s;

    &:hover {
      color: #fff;
      background-color: #2B5E91;
    }

    &.active {
      background-color: #2B5E91;
    }
  }

  &__button {
    font-family: 'Roboto', sans-serif;
    color: #fff;
    background-color: #326EAA;
    font-size: 14px;
    text-transform: uppercase;
    border: 0;
    border-radius: 30px;
    padding: 15px 10px;
    transition: background-color 0.5s;
    margin-left: 20px;

    &:hover {
      cursor: pointer;
      background-color: #2B5E91;
    }
  }
}
